import styled from "styled-components";

import Select from "../../assets/UI/Select";
import { A } from "../../assets/styles/component";

import { EngLoc } from "../../assets/constants/localizations";

import logo_desk from "../../assets/images/Elements/logo_desktop.svg";
import ModalSignUp from "../../assets/UI/SignUp";
import { useState } from "react";

const StyledHeader = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 6.938rem;

  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  font-weight: 400;
  font-size: 1.125rem;
  z-index: 999;

  transition: 0.25s;

  ${({ scrollY }) =>
    scrollY <= 100
      ? ""
      : `
    background: rgba(71, 8, 204, 0.65);
    height: 4.875rem;
    backdrop-filter: blur(10px)
    `}
`;

const Nav = styled.nav`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 15%);
  max-width: 100rem;
  margin: 0 8.3%;

  @media (max-width: 1200px) {
    width: calc(100vw - 10%);
    margin: 0 3.3%;
  }

  button {
    font-family: "HeliosExtC", -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    border: 0;
    outline: 0;
    color: #fff;
    width: 138px;
    height: 50px;
    background: linear-gradient(217.29deg, #0e7ed8 10.81%, #04a2e4 92.27%);
    border-radius: 63px;
  }
`;

const NavTabs = styled.div`
  display: flex;
  gap: clamp(1.875rem, 4vw, 4.125rem);

  & a {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.063rem;
  }
`;

const Logo = styled.img`
  aspect-ratio: 128 / 62;
`;

const Header = ({ navItem, language, scrollY, changeLng }) => {
  const changeLocalization = (lang = "Ru") => {
    localStorage.setItem("Language", lang);
    changeLng(lang);
  };

  const [open, setOpen] = useState(false);

  return (
    <>
      <StyledHeader scrollY={scrollY}>
        <Nav>
          <Logo
            width="128"
            height="62"
            loading="lazy"
            src={logo_desk}
            alt="За стеклом"
          />
          <NavTabs>
            {navItem.map((item, index) => (
              <A
                href={item.href}
                size="18"
                key={index}
                target={item.blank && "_blank"}
                aria-label={"Перейти на раздел " + item.name}
              >
                {item.name}
              </A>
            ))}
          </NavTabs>
          <div style={{ display: "flex", gap: 15 + "px" }}>
            {/* <Select
            type="languages"
            name={language === "Ru" ? "Ru" : "Eng"}
            paddingX="17"
            color="#FFFFFF"
            bg_color="rgba(255, 255, 255, 0.03)"
            border={true}
            onChange={changeLocalization}
          ></Select> */}
            <button onClick={() => setOpen(true)}>
              {language === "Ru" ? "Войти" : EngLoc.buttons.login}
            </button>
          </div>
        </Nav>
      </StyledHeader>
      <ModalSignUp open={open} setOpen={setOpen} />
    </>
  );
};

export default Header;
